header {
  height: 100%;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* CTA*/

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* Header Social*/

.header__socials {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

/* me */

.me {
  background:linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
   position: relative;
  left: calc(50% - 11rem);
  margin-top: 3rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem; 
}

/*Scroll down*/

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*dispositivos medios 1024 px*/

@media (max-width: 1024px) {
  header{
    height:68vh;

  }
  

  section {
    margin-top: 6rem;
  }
}

/*dispositivos pequeÃ±os 600 px*/

@media (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;

    }
  }