@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400 ease;

  --container-whidth-lg: 75%;
  --container-whidth-md: 86%;
  --container-whidth-ms: 90%;
}

html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
}

/*estilos generales*/

.container {
  max-width: var(--container-whidth-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font: size 2.5rem;
}

section {
    margin-top:8rem;
    height:100%;
}

section > h2, section >h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 2rem;
}

.text-light {
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition)
}

a:hover{
    color: var(--color-white);
}

.btn {
    width:max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor:pointer;
    border:1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background-color: var(--color-primary);
    color: var(--color-bg);
    border-color: transparent;
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*dispositivos medios 1024 px*/

@media (max-width: 1024px) {
  .container {
    width: var(--container-whidth-md);
  }

  section{
        margin-top: 6rem;
  }
}

/*dispositivos pequeÃ±os 600 px*/

@media (max-width: 600px) {
    .container {
      width: var(--container-whidth-sm);
    }
  
    section >h2{
          margin-bottom: 2rem;
    }
  }
