footer {
    background:var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size:0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
    text-decoration: none;
}

.footer__logo {
    font-size:2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:2rem;
    margin:0 auto 3rem;
}

.footer__social {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom:3rem;
}

.footer__social a {
    background: var(--color-bg);
    color: var(--color-white);
    padding:0.08rem;
    border-radius: 0.7rem;
    display: flex;
    border-radius: 1px solid trasparent;
}

.footer__social a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copy {
    margin-bottom: 4rem;
    color:var(--color-bg)
}